import { __ } from '@wordpress/i18n';
import { PanelBody, PanelRow, RangeControl, ToggleControl } from '@wordpress/components';

import { Label, BorderControl } from '../../../../../../Components';
import { BControlPro, SelectControlPro } from '../../../../../../Components/Pro';

import { effects } from '../../../../utils/options';

const Options = ({ attributes, setAttributes, premiumProps, updateOptions }) => {
	const { carouselOptions, isPage, isPrevNext } = attributes;
	const { isLoop, allowTouchMove = false, speed, autoplayOptions, freeMode = {}, effect = 'slide', mousewheel = {}, pagination = {}, navigation = {} } = carouselOptions;
	const { enabled: isAutoplay, delay = 1.5, disableOnInteraction = false, reverseDirection = false, pauseOnMouseEnter = false, stopOnLastSlide = false } = autoplayOptions;

	const panelBodyIF = {
		className: 'bPlPanelBody',
		initialOpen: false
	}

	return <>
		<PanelBody className='bPlPanelBody' title={__('Basic', 'carousel-block')}>
			<ToggleControl label={__('Enable Loop', 'carousel-block')} checked={isLoop} onChange={val => updateOptions('isLoop', val)} />
			<small>{__('Total number of slides must be more then column count', 'carousel-block')}</small>

			<BControlPro label={__('Enable Touch Move', 'carousel-block')} checked={allowTouchMove} onChange={val => updateOptions('allowTouchMove', val)} {...premiumProps} Component={ToggleControl} />

			<Label>{__('Speed (s):', 'carousel-block')}</Label>
			<RangeControl value={speed} onChange={val => updateOptions('speed', val)} min={0} max={10} step={.05} />
			<small>{__('Smaller speed will be slide faster', 'carousel-block')}</small>
		</PanelBody>


		<PanelBody title={__('Autoplay', 'carousel-block')} {...panelBodyIF}>
			<ToggleControl label={__('Enable Autoplay', 'carousel-block')} checked={isAutoplay} onChange={val => updateOptions('autoplayOptions', val, 'enabled')} />
			<small>{__('Autoplay will not work in backend', 'carousel-block')}</small>

			{isAutoplay && <>
				<Label>{__('Autoplay Delay (s):', 'carousel-block')}</Label>
				<RangeControl value={delay} onChange={val => updateOptions('autoplayOptions', val, 'delay')} min={0} max={10} step={.05} />
				<small>{__('Smaller delay will be autoplay faster', 'carousel-block')}</small>

				<BControlPro className='mt20' label={__('Disable on Interaction', 'carousel-block')} checked={disableOnInteraction} onChange={val => updateOptions('autoplayOptions', val, 'disableOnInteraction')} {...premiumProps} Component={ToggleControl} />

				<BControlPro className='mt10' label={__('Pause on Mouse Enter', 'carousel-block')} checked={pauseOnMouseEnter} onChange={val => updateOptions('autoplayOptions', val, 'pauseOnMouseEnter')} {...premiumProps} Component={ToggleControl} />
				<small>{__(`If 'Disable on Interaction' is also enabled, it will stop autoplay instead of pause`, 'carousel-block')}</small>

				<BControlPro className='mt10' label={__('Reverse Direction', 'carousel-block')} checked={reverseDirection} onChange={val => updateOptions('autoplayOptions', val, 'reverseDirection')} {...premiumProps} Component={ToggleControl} />

				<BControlPro className='mt10' label={__('Stop on Last Slide', 'carousel-block')} checked={stopOnLastSlide} onChange={val => updateOptions('autoplayOptions', val, 'stopOnLastSlide')} {...premiumProps} Component={ToggleControl} />
			</>}
		</PanelBody>


		<PanelBody title={__('Free Mode', 'carousel-block')} {...panelBodyIF}>
			<BControlPro label={__('Enable Free Mode', 'carousel-block')} checked={freeMode.enabled} onChange={val => updateOptions('freeMode', val, 'enabled')} {...premiumProps} Component={ToggleControl} />
			<small>{__('Smooth Scrolling with dragging the slide', 'carousel-block')}</small>
			<br />
			<small>{__('Enable Touch Move to use Free Mode', 'carousel-block')}</small>

			{freeMode.enabled && <BControlPro className='mt10' label={__('Sticky', 'carousel-block')} checked={freeMode.sticky} onChange={val => updateOptions('freeMode', val, 'sticky')} {...premiumProps} Component={ToggleControl} />}
		</PanelBody>


		<PanelBody title={__('Effects', 'carousel-block')} {...panelBodyIF}>
			<PanelRow>
				<Label className=''>{__('Effect:', 'carousel-block')}</Label>
				<SelectControlPro value={effect}
					onChange={val => {
						setAttributes({
							carouselOptions: {
								...carouselOptions,
								effect: val,
								columns: ['slide', 'coverflow'].includes(val) ? { desktop: 3, tablet: 2, mobile: 1 } : { desktop: 1, tablet: 1, mobile: 1 }
							},

							width: ['cube', 'flip', 'cards'].includes(val) ? { desktop: '350px', tablet: '350px', mobile: '350px' } : { desktop: '100%', tablet: '100%', mobile: '100%' },

							height: ['cube', 'flip', 'cards'].includes(val) ? { desktop: '450px', tablet: '450px', mobile: '450px' } : { desktop: '350px', tablet: '300px', mobile: '250px' },

							padding: ['cube', 'flip', 'cards'].includes(val) ? { top: '0px', right: '50px', bottom: '30px', left: '50px' } : { vertical: '10px', horizontal: '10px' }
						});
					}}
					options={effects}
					proValues={['cube', 'coverflow', 'flip', 'cards']}
					{...premiumProps}
				/>
			</PanelRow>
			<small>{__('To work fade, cube, creative, flip & cards effects properly, set single column per view.', 'carousel-block')}</small>
			<br />
			<small>{__('Some settings may change when effect will be changed.', 'carousel-block')}</small>
		</PanelBody>


		<PanelBody title={__('Mousewheel', 'carousel-block')} {...panelBodyIF}>
			<BControlPro label={__('Enable Slide on Mousewheel', 'carousel-block')} checked={mousewheel.enabled} onChange={val => updateOptions('mousewheel', val, 'enabled')}{...premiumProps} Component={ToggleControl} />
		</PanelBody>


		<PanelBody title={__('Pagination', 'carousel-block')} {...panelBodyIF}>
			<ToggleControl label={__('Show Pagination', 'carousel-block')} checked={isPage} onChange={val => setAttributes({ isPage: val })} />

			{isPage && <>
				<BControlPro className='mt10' label={__('Show On Tablet', 'carousel-block')} checked={pagination.inTablet} onChange={val => updateOptions('pagination', val, 'inTablet')} {...premiumProps} Component={ToggleControl} />

				<BControlPro className='mt10' label={__('Show On Mobile', 'carousel-block')} checked={pagination.inMobile} onChange={val => updateOptions('pagination', val, 'inMobile')} {...premiumProps} Component={ToggleControl} />

				<ToggleControl className='mt10' label={__('Enable Pagination Clickable', 'carousel-block')} checked={pagination.clickable} onChange={val => updateOptions('pagination', val, 'clickable')} />

				<BControlPro className='mt10' label={__('Enable Pagination Dynamic Bullets', 'carousel-block')} checked={pagination.dynamicBullets} onChange={val => updateOptions('pagination', val, 'dynamicBullets')} {...premiumProps} Component={ToggleControl} />

				<BorderControl label={__('Pagination Border:', 'carousel-block')} value={pagination.border} onChange={val => updateOptions('pagination', val, 'border')} />
			</>}
		</PanelBody>


		<PanelBody title={__('Navigation', 'carousel-block')} {...panelBodyIF}>
			<ToggleControl label={__('Show Navigation', 'carousel-block')} checked={isPrevNext} onChange={val => setAttributes({ isPrevNext: val })} />

			{isPrevNext && <>
				<BControlPro className='mt10' label={__('Show On Tablet', 'carousel-block')} checked={navigation.inTablet} onChange={val => updateOptions('navigation', val, 'inTablet')} {...premiumProps} Component={ToggleControl} />

				<BControlPro className='mt10' label={__('Show On Mobile', 'carousel-block')} checked={navigation.inMobile} onChange={val => updateOptions('navigation', val, 'inMobile')} {...premiumProps} Component={ToggleControl} />
			</>}
		</PanelBody>
	</>
}
export default Options;