import { __ } from '@wordpress/i18n';
import { TextControl } from '@wordpress/components';
import { produce } from 'immer';

import { Label, InlineDetailMediaUpload } from '../../../../../../Components';
import { SelectControlPro } from '../../../../../../Components/Pro';

import { actions } from '../../../../utils/options';

const Item = ({ attributes, setAttributes, arrKey, index, setActiveIndex = false, premiumProps }) => {
	const items = attributes[arrKey];
	const { image, action = 'none', link = '', caption } = items[index];

	const updateCarousel = (index, property, val, childProperty = null) => {
		const newItems = produce(attributes[arrKey], draft => {
			if (null !== childProperty) {
				draft[index][property][childProperty] = val;
			} else {
				draft[index][property] = val;
			}
		});

		setAttributes({ [arrKey]: newItems });
		setActiveIndex && setActiveIndex(index);
	}

	return <>
		<Label className='mb5'>{__('Image File:', 'carousel-block')}</Label>
		<InlineDetailMediaUpload value={image} onChange={val => updateCarousel(index, 'image', val)} placeholder={__('Enter Image URL', 'carousel-block')} />

		<TextControl className='mt20' label={__('Caption:', 'carousel-block')} value={caption} onChange={val => updateCarousel(index, 'caption', val)} />

		<SelectControlPro className='mt20' label={__('Click Action', 'carousel-block')} labelPosition='left' value={action} onChange={val => updateCarousel(index, 'action', val)} options={actions} proValues={['lightbox']} {...premiumProps} />

		{'link' === action && <TextControl className='mt20' label={__('Link:', 'carousel-block')} value={link} onChange={val => updateCarousel(index, 'link', val)} />}
	</>
}
export default Item;