import { useRef } from 'react';
import { __ } from '@wordpress/i18n';
import { PanelBody, PanelRow } from '@wordpress/components';

import { Label } from '../../../../../../Components';

const ShortCode = ({ currentPostId }) => {
	const tooltip = useRef(null);

	const handleShortcode = (e) => {
		const input = e.target;
		input.select();
		input.setSelectionRange(0, 30);
		document.execCommand('copy');
		tooltip.current.innerHTML = __('Copied Successfully!', 'carousel-block');
		setTimeout(() => {
			tooltip.current.innerHTML = __('Copy To Clipboard', 'carousel-block');
		}, 1500);
	};

	return <PanelBody className='bPlPanelBody'>
		<Label className='mb5'>{__('Copy the shortcode and use it anywhere.', 'carousel-block')}</Label>
		<PanelRow>
			<div className='bicbFrontShortcode'>
				<input onClick={handleShortcode} value={`[bicb id=${currentPostId}]`} />
				<span ref={tooltip} className='tooltip'>
					{__('Copy To Clipboard', 'carousel-block')}
				</span>
			</div>
		</PanelRow>
	</PanelBody>
}
export default ShortCode;