import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { withSelect } from '@wordpress/data';
import { PanelBody, PanelRow, RangeControl, SelectControl, ToggleControl, __experimentalUnitControl as UnitControl } from '@wordpress/components';

import { Label, BDevice, HelpPanel, ItemsPanel, BtnGroup } from '../../../../../../Components';
import { BControlPro, SelectControlPro } from '../../../../../../Components/Pro';
import { pxUnit, perUnit, emUnit, vhUnit } from '../../../../../../Components/utils/options';

import { aligns, capDisplays, capPositions, imgWidths, linkOns, verticalPositions } from '../../../../utils/options';
import Item from './Item';
import ShortCode from './ShortCode';

const General = ({ attributes, setAttributes, setActiveIndex, premiumProps, updateOptions, currentPostType, currentPostId }) => {
	const { width = {}, height = {}, elements = {}, carouselOptions, image = { width: '100%' }, caption = {} } = attributes;
	const { columns, columnGap } = carouselOptions;
	const { display, position, verticalAlign, textAlign } = caption;

	const [device, setDevice] = useState('desktop');
	const [wDevice, setWDevice] = useState('desktop');
	const [hDevice, setHDevice] = useState('desktop');

	const panelBodyIF = {
		className: 'bPlPanelBody',
		initialOpen: false
	}

	return <>
		{currentPostType === 'bicb' && <ShortCode currentPostId={currentPostId} />}

		<HelpPanel slug='b-carousel-block' docsLink='https://bplugins.com/docs/b-carousel-block/' />


		<PanelBody className='bPlPanelBody' title={__('Carousel Items', 'carousel-block')}>
			<ItemsPanel attributes={attributes} setAttributes={setAttributes} arrKey='carousels' newItem={{
				image: { id: null, url: '', alt: '', title: '' },
				action: 'none',
				link: '',
				caption: ''
			}} ItemSettings={Item} itemLabel='Carousel' setActiveIndex={setActiveIndex} premiumProps={premiumProps} />
		</PanelBody>


		<PanelBody title={__('Layout', 'carousel-block')} {...panelBodyIF}>
			<PanelRow>
				<Label className='mb5'>{__('Width:', 'carousel-block')}</Label>
				<BDevice device={wDevice} onChange={val => setWDevice(val)} />
			</PanelRow>
			<UnitControl value={width[wDevice]} onChange={val => setAttributes({ width: { ...width, [wDevice]: val } })} units={[pxUnit(960), perUnit(100), emUnit(60)]} />

			<PanelRow className='mt20'>
				<Label className='mb5'>{__('Height:', 'carousel-block')}</Label>
				<BDevice device={hDevice} onChange={val => setHDevice(val)} />
			</PanelRow>
			<UnitControl value={height[hDevice]} onChange={val => setAttributes({ height: { ...height, [hDevice]: val } })} units={[pxUnit(250), vhUnit(30), emUnit(16)]} />

			<PanelRow className='mt20'>
				<Label className='mb5'>{__('Columns:', 'carousel-block')}</Label>
				<BDevice device={device} onChange={val => setDevice(val)} />
			</PanelRow>
			<RangeControl value={columns[device]} onChange={val => updateOptions('columns', { ...columns, [device]: val })} min={1} max={6} step={1} beforeIcon='grid-view' />

			<Label>{__('Column Gap:', 'carousel-block')}</Label>
			<RangeControl value={columnGap} onChange={val => updateOptions('columnGap', val)} min={0} max={250} step={1} beforeIcon='arrow-right-alt' />
		</PanelBody>


		<PanelBody title={__('Image', 'carousel-block')} {...panelBodyIF}>
			<BtnGroup label={__('Width', 'carousel-block')} value={image?.width} onChange={val => setAttributes({ image: { ...image, width: val } })} options={imgWidths} />
			{/* <SelectControl label={__('Width', 'carousel-block')} labelPosition='left' value={image?.width} onChange={val => setAttributes({ image: { ...image, width: val } })} options={imgWidths} /> */}
		</PanelBody>


		<PanelBody title={__('Caption', 'carousel-block')} {...panelBodyIF}>
			<SelectControlPro label={__('Display', 'carousel-block')} labelPosition='left' value={display} onChange={val => setAttributes({ caption: { ...caption, display: val } })} options={capDisplays} proValues={['hover']} {...premiumProps} />
			<small>{__('Hover view will work only if the caption position is on the image.', 'carousel-block')}</small>

			<SelectControlPro className='mt20' label={__('Position', 'carousel-block')} labelPosition='left' value={position} onChange={val => setAttributes({ caption: { ...caption, position: val } })} options={capPositions} proValues={['bellowImage']} {...premiumProps} />

			{'onImage' === position && <>
				<BControlPro className='mt20' label={__('Vertical Align', 'carousel-block')} labelPosition='left' value={verticalAlign} onChange={val => setAttributes({ caption: { ...caption, verticalAlign: val } })} options={verticalPositions} {...premiumProps} Component={SelectControl} />
			</>}

			<PanelRow className='mt20'>
				<Label className=''>{__('Text Align:', 'carousel-block')}</Label>
				<BtnGroup value={textAlign} onChange={val => setAttributes({ caption: { ...caption, textAlign: val } })} options={aligns} isIcon={true} />
			</PanelRow>
		</PanelBody>


		<PanelBody title={__('Slide Link', 'carousel-block')} {...panelBodyIF}>
			<ToggleControl label={__('Open link in new tab?', 'carousel-block')} checked={elements?.linkNewTab} onChange={val => setAttributes({ elements: { ...elements, linkNewTab: val } })} />

			<SelectControlPro className='mt20' label={__('Link On', 'carousel-block')} labelPosition='left' value={elements?.linkOn} onChange={val => setAttributes({ elements: { ...elements, linkOn: val } })} options={linkOns} proValues={['caption']} {...premiumProps} />
		</PanelBody>
	</>
}
export default withSelect((select) => {
	const { getCurrentPostType, getCurrentPostId } = select('core/editor');

	return {
		currentPostType: getCurrentPostType(),

		currentPostId: getCurrentPostId()
	}
})(General);