import { useState } from 'react';
import { __ } from '@wordpress/i18n';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { TabPanel } from '@wordpress/components';
import { produce } from 'immer';

import { AboutPro } from '../../../../../Components/Pro';

import { generalStyleTabs } from '../../../utils/options';
import General from './General/General';
import Options from './Options/Options';
import Style from './Style/Style';

const Settings = ({ attributes, setAttributes, setActiveIndex, isPremium }) => {
	const { alignment = 'center', carouselOptions } = attributes;

	const [aboutProOpen, setAboutProOpen] = useState(false);

	const updateOptions = (type, val, otherType = false) => {
		const newOptions = produce(carouselOptions, draft => {
			if (otherType) {
				draft[type][otherType] = val;
			} else {
				draft[type] = val;
			}
		});
		setAttributes({ carouselOptions: newOptions });
	}

	const premiumProps = {
		isPremium,
		setOpen: setAboutProOpen
	};

	const commonProps = { attributes, setAttributes }
	const genOptProps = { ...commonProps, premiumProps, updateOptions }

	return <>
		<InspectorControls>
			{!isPremium && <div className='bicbInspectorInfo'>
				Need more block like this? Checkout the bundle ➡ <a href='https://wordpress.org/plugins/b-blocks' target='_blank' rel='noopener noreferrer'>B Blocks</a>
			</div>}

			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <General {...genOptProps} setActiveIndex={setActiveIndex} />}


				{'options' === tab.name && <Options {...genOptProps} />}


				{'style' === tab.name && <Style {...commonProps} />}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={alignment} onChange={val => setAttributes({ alignment: val })} describedBy={__('Carousel Alignment')} alignmentControls={[
				{ title: __('Carousel in left', 'carousel-block'), align: 'left', icon: 'align-left' },
				{ title: __('Carousel in center', 'carousel-block'), align: 'center', icon: 'align-center' },
				{ title: __('Carousel in right', 'carousel-block'), align: 'right', icon: 'align-right' }
			]} />
		</BlockControls>


		<AboutPro aboutProOpen={aboutProOpen} setAboutProOpen={setAboutProOpen} link='https://bplugins.com/products/b-carousel-block/#pricing'>
			<li>&emsp;<strong>{__('Image in Lightbox: ', 'carousel-block')}</strong>{__('Open the slide image in the lightbox.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Link on Caption: ', 'carousel-block')}</strong>{__('Set the slide link on the caption.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Touch Move: ', 'carousel-block')}</strong>{__('Control the slider with touch move.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Disable on Interaction: ', 'carousel-block')}</strong>{__('Disable on Interaction option in autoplay.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Pause on Mouse Enter: ', 'carousel-block')}</strong>{__('Pause on Mouse Enter option in autoplay.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Reverse Direction: ', 'carousel-block')}</strong>{__('Reverse Direction option in autoplay.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Stop on Last Slide: ', 'carousel-block')}</strong>{__('Stop on Last Slide option in autoplay.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Free Mode: ', 'carousel-block')}</strong>{__('Free Mode option on drag slide.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Sticky: ', 'carousel-block')}</strong>{__('Sticky option in free mode.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Effects: ', 'carousel-block')}</strong>{__('Explore more effects such as Cube, Coverflow, Flip, and Cards.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Slide on Mousewheel: ', 'carousel-block')}</strong>{__('Slide on Mousewheel option.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Pagination Device: ', 'carousel-block')}</strong>{__('Show/Hide pagination in different devices(tablet and mobile).', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Dynamic Bullets: ', 'carousel-block')}</strong>{__('Option for pagination dynamic bullets.', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Navigation Device: ', 'carousel-block')}</strong>{__('Show/Hide navigation in different devices(tablet and mobile).', 'carousel-block')}</li>

			<li>&emsp;<strong>{__('Caption: ', 'carousel-block')}</strong>{__('Show Caption on hover, Set position outside of the image, Change vertical align.', 'carousel-block')}</li>
		</AboutPro>
	</>;
};
export default Settings;