import { useEffect } from 'react';

import { useWPAjax, useWPOptionQuery } from '../../../react-utils/hooks';

const usePremiumInEditor = () => {
	const { data: bicbUtils } = useWPOptionQuery('bicbUtils');
	const { data = null, refetch, isLoading = true } = useWPAjax('bicbPipeChecker', { _wpnonce: bicbUtils?.nonce }, true);
	const isPremium = (!isLoading && data?.isPipe) || false;

	useEffect(() => {
		refetch();
	}, [bicbUtils]);

	return { isPremium, isLoading };
};
export default usePremiumInEditor;